import {
  ADD_HINT,
  CONTINUE_GAME,
  CORRECT_ANSWER,
  GET_UNCOMPLITED_GAME,
  LOSING,
  SET_THEME,
  SET_WORD,
  SWITCH_GAME_MODE,
  USE_HINT,
  WINNING,
  WRONG_ANSWER,
} from "./actionsType"

const initialState = {
  theme: {},
  disabledButtons: false,

  word: null,
  answerArr: null,

  helth: 0,
  currentHelth: 0,
  hints: 10,
  correctLetters: [],
  wrongLetters: [],
  activeGame: null,

  mode: "answer",
}

export const gameReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_THEME: {
      return {
        ...state,
        theme: { title: action.payload.theme, i: action.payload.i },
      }
    }
    case SET_WORD:
      return {
        ...state,
        word: action.payload.word,
        answerArr: action.payload.answerArr,
        //lettersHint: action.payload.lettersHint,
        helth: action.payload.helth,
        currentHelth: action.payload.helth,
        correctLetters: [],
        wrongLetters: [],
        disabledButtons: false,
      }
    case WRONG_ANSWER:
      return {
        ...state,
        currentHelth: action.payload.currentHelth,
        wrongLetters: action.payload.wrongLet,
      }
    case CORRECT_ANSWER:
      return {
        ...state,
        correctLetters: action.payload.correctLet,
        answerArr: action.payload.newAnswerArr,
      }
    case USE_HINT:
      return {
        ...state,
        answerArr: action.payload.newAnswerArr,
        correctLetters: action.payload.correctLet,
        wrongLetters: action.payload.wrongLet,
        hints: action.payload.hints,
      }

    case ADD_HINT:
      return { ...state, hints: action.payload }

    case WINNING:
      return { ...state, disabledButtons: true }

    case LOSING:
      return { ...state, answerArr: action.payload, disabledButtons: true }

    case GET_UNCOMPLITED_GAME:
      return { ...state, activeGame: action.payload }

    case CONTINUE_GAME:
      return {
        ...state,
        theme: {
          title: state.activeGame.theme.theme,
          i: state.activeGame.theme.i,
        },
        answerArr: state.activeGame.answerArr,
        helth: state.activeGame.helth,
        currentHelth: state.activeGame.currentHelth,
        correctLetters: state.activeGame.correctLetters,
        wrongLetters: state.activeGame.wrongLetters,
      }

    case SWITCH_GAME_MODE:
      return { ...state, mode: action.payload }
    default:
      return state
  }
}
