import {Tappable} from '@vkontakte/vkui';

import { classNames } from '@vkontakte/vkui/';


const SliderSwitchIconsButton = (props) => {
  const { active, hovered, children, getRootRef, ...restProps } = props;
  return <Tappable {...restProps}
    className={
      classNames(
        'SliderSwitchIcons__button',
        {
          ['SliderSwitchIcons__button--hover']:  hovered,
        },
      )
    }
    Component="button"
    type="button"
    getRootRef={getRootRef}
    aria-pressed={active}
    tabIndex={0}
    hasActive={false}
    hoverMode="opacity"
  >
    {children}
  </Tappable>;
};

export default SliderSwitchIconsButton;