import React, {  createRef } from 'react';
import SliderSwitchIconsButton from './SliderSwitchIconsButton';
import { classNames } from '@vkontakte/vkui/';
import './SliderSwitchIcons.css'

export default class SliderSwitchIcons extends React.Component {
   constructor(props) {
    super(props);

    this.state = {
      activeValue: props.activeValue ?? '',
      hoveredOptionId: -1,
    };

    this.firstButton = createRef();
    this.secondButton = createRef();
  }

  static defaultProps = {
    options: [{ icon: '', value: '' }, { icon: '', value: '' }],
  };


  onSwitch = (value) => {
    const { onSwitch } = this.props;

    this.setState(() => ({
      activeValue: value,
    }), () => {
      onSwitch && onSwitch(value);
    });
  };

  handleFirstClick = () => {
    const { options } = this.props;
    const { value } = options[0];

    this.onSwitch(value);
  };

  handleSecondClick = () => {
    const { options } = this.props;
    const { value } = options[1];

    this.onSwitch(value);
  };

  handleFirstHover = () => {
    this.setState(() =>({
      hoveredOptionId: 0,
    }));
  };

  handleSecondHover = () => {
    this.setState(() =>({
      hoveredOptionId: 1,
    }));
  };

  resetFocusedOption = () => {
    this.setState(() => ({
      hoveredOptionId: -1,
    }));
  };

  render() {
    const { options, activeValue: _activeValue, onSwitch, ...restProps } = this.props;
    const { activeValue, hoveredOptionId } = this.state;

    const [firstOption, secondOption] = options;
    const firstActive = firstOption.value === activeValue;
    const secondActive = secondOption.value === activeValue;

    return (
      <div
        {...restProps}
        className="SliderSwitchIcons"
        onMouseLeave={this.resetFocusedOption}
      >
        <div className={classNames(
          'SliderSwitchIcons__slider',
          {
            ['SliderSwitchIcons--firstActive']: firstActive,
            ['SliderSwitchIcons--secondActive']: secondActive,
          },
        )} />
        <input type="hidden" value={activeValue} />
        <SliderSwitchIconsButton
          active={firstActive}
          hovered={hoveredOptionId === 0}
          aria-pressed={firstActive}
          onClick={this.handleFirstClick}
          onMouseEnter={this.handleFirstHover}
          getRootRef={this.firstButton}
        >
          {firstOption.icon}
        </SliderSwitchIconsButton>
        <SliderSwitchIconsButton
          active={secondActive}
          hovered={hoveredOptionId === 1}
          onClick={this.handleSecondClick}
          onMouseEnter={this.handleSecondHover}
          getRootRef={this.secondButton}
        >
          {secondOption.icon}
        </SliderSwitchIconsButton>
      </div>
    );
  }
}