import React from "react"
import { useDispatch, useSelector } from "react-redux"

import {
  Button,
  Panel,
  PanelHeader,
  PanelHeaderBack,
  Placeholder,
  SimpleCell,
} from "@vkontakte/vkui"
import { Icon28GameOutline, Icon56GhostOutline } from "@vkontakte/icons"
import { goBack, setStory } from "../store/router/actions"
import { convertDateToInputFormat } from "@vkontakte/vkjs"
import { setTheme } from "../store/game/actions"

const ResolvedWords = (props) => {
  const dispatch = useDispatch()
  const { resolvedWords } = useSelector((state) => state.vk)

  return (
    <Panel id={props.id}>
      <PanelHeader
        left={<PanelHeaderBack onClick={() => dispatch(goBack())} />}
      >
        
      </PanelHeader>
      {!resolvedWords.length && (
        <Placeholder
          stretched
          header='  '
          action={
            <Button
              size='m'
              before={<Icon28GameOutline />}
              onClick={() => {
                dispatch(setStory("game", "start"))
                dispatch(setTheme("", 0))
              }}
            >
              
            </Button>
          }
          icon={
            <Icon56GhostOutline
              fill='var(--dynamic_blue)'
              width={96}
              height={96}
            />
          }
        >
              
        </Placeholder>
      )}
      {!!resolvedWords.length &&
        resolvedWords.map((word, index) => (
          <SimpleCell
            description={new Intl.DateTimeFormat("ru", {
              month: "long",
              day: "2-digit",
              hour: "numeric",
              minute: "numeric",
            }).format(word.date)}
            key={index}
          >
            {word.word}
          </SimpleCell>
        ))}
    </Panel>
  )
}
export default ResolvedWords
