import React from "react"

import {
  Button,
  CellButton,
  Div,
  Group,
  Header,
  List,
  Panel,
  PanelHeader,
  SimpleCell,
} from "@vkontakte/vkui"
import { Icon28Play } from "@vkontakte/icons"

import { useDispatch, useSelector } from "react-redux"
import { categories } from "../components/categories"
import { continueGame, setTheme } from "../store/game/actions"

const StartGame = (props) => {
  const dispatch = useDispatch()
  const { activeGame } = useSelector((state) => state.game)
  return (
    <Panel id={props.id}>
      <PanelHeader> </PanelHeader>
      <Group header={<Header mode='secondary'> </Header>}>
        <List>
          {categories.map((theme, i) => (
            <SimpleCell
              onClick={() => dispatch(setTheme(theme.title, i))}
              expandable
              before={theme.icon}
              key={i}
            >
              {theme.title}
            </SimpleCell>
          ))}
        </List>
      </Group>
      {activeGame && (
        <CellButton
          centered
          before={<Icon28Play />}
          onClick={() => dispatch(continueGame())}
        >
          
        </CellButton>
      )}
    </Panel>
  )
}
export default StartGame
