import React, { useEffect } from "react"
import bridge from "@vkontakte/vk-bridge"

import {
  ConfigProvider,
  AdaptivityProvider,
  AppRoot,
  Epic,
  View,
  SplitLayout,
} from "@vkontakte/vkui"

import "@vkontakte/vkui/dist/vkui.css"

import "./styles/style.css"

import AppTabbar from "./components/AppTabbar"

import { useDispatch, useSelector } from "react-redux"
import { getFetchedUser, getUserData, setScheme } from "./store/vk/actions"
import Profile from "./panels/Profile"
import Game from "./panels/Game"
import StartGame from "./panels/StartGame"
import { goBack } from "./store/router/actions"
import Statistics from "./panels/Statistics"
import ResolvedWords from "./panels/ResolvedWords"

const App = () => {
  const dispatch = useDispatch()

  const { scheme } = useSelector((state) => state.vk)
  const { activeStory, activePanel, popout } = useSelector(
    (state) => state.router
  )

  useEffect(() => {
    bridge.send("VKWebAppInit")
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        console.log(data)
        dispatch(setScheme(data.scheme))
      }
    })

    window.onpopstate = () => dispatch(goBack())

    dispatch(getFetchedUser())
    dispatch(getUserData())
  }, [])

  return (
    <ConfigProvider scheme={scheme}>
      <AdaptivityProvider>
        <AppRoot>
          <SplitLayout popout={popout}>
            <Epic
              activeStory={activeStory}
              tabbar={activePanel === "game" ? null : <AppTabbar />}
            >
              <View id='game' activePanel={activePanel}>
                <StartGame id='start' />
                <Game id='game' />
              </View>
              <View id='profile' activePanel={activePanel}>
                <Profile id='profile' />
                <Statistics id='statistics' />
                <ResolvedWords id='resolvedWords' />
              </View>
            </Epic>
          </SplitLayout>
        </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  )
}

export default App
