import React from "react"
import { Button, PopoutWrapper } from "@vkontakte/vkui"
import Alert from "./AlertHints/Alert"
import { useDispatch } from "react-redux"
import { closePopout } from "../store/router/actions"
import { addHints } from "../store/vk/actions"

const HintsAlert = () => {
  const dispatch = useDispatch()
  return (
    <PopoutWrapper alignY='center' alignX='center'>
      <Alert
        actions={[
          {
            title: "",
            autoclose: true,
            mode: "cancel",
          },
          {
            title: "",
            autoclose: true,
            mode: "default",
            action: () => dispatch(addHints()),
          },
        ]}
        header=' '
        onClose={() => dispatch(closePopout())}
      >
              10 
      </Alert>
    </PopoutWrapper>
  )
}
export default HintsAlert
