import { useSelector } from "react-redux"

import { Icon28Like, Icon28LikeOutline } from "@vkontakte/icons"

const IconsHelth = (props) => {
  const color = "var(--dynamic_red)"

  const { helth, currentHelth } = useSelector((state) => state.game)

  return Array(helth)
    .fill("")
    .map((a, i) => {
      if (helth == currentHelth) return <Icon28Like fill={color} key={i} />
      if (i + 1 <= currentHelth) return <Icon28Like fill={color} key={i} />
      return <Icon28LikeOutline fill={color} key={i} />
    })
}

export default IconsHelth
