import {
  INCREMENT_GAMES,
  INCREMENT_LOOSE,
  INCREMENT_USED_HINTS,
  INCREMENT_WINS,
  GET_FETCHED_USER,
  SET_SCHEME,
  GET_USER_DATA,
  UPDATE_RESOLVED_WORDS,
} from "./actionTypes"

const initialState = {
  user: {
    id: 0,
    first_name: 1,
    last_name: 2,
    photo_200: 0,
  },
  statistics: { totalGames: 0, wins: 0, loose: 0, usedHints: 0 },
  resolvedWords: [], //
  scheme: "",
}

export const vkReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_SCHEME:
      return { ...state, scheme: action.payload }
    case GET_FETCHED_USER:
      return { ...state, user: action.payload }
    case GET_USER_DATA:
      return {
        ...state,
        statistics: action.payload.statistics,
        resolvedWords: action.payload.resolvedWords,
      }

    case INCREMENT_GAMES:
      return {
        ...state,
        statistics: {
          ...state.statistics,
          totalGames: state.statistics.totalGames + 1,
        },
      }
    case INCREMENT_WINS:
      return {
        ...state,
        statistics: {
          ...state.statistics,
          wins: state.statistics.wins + 1,
        },
      }
    case INCREMENT_LOOSE:
      return {
        ...state,
        statistics: {
          ...state.statistics,
          loose: state.statistics.loose + 1,
        },
      }
    case INCREMENT_USED_HINTS:
      return {
        ...state,
        statistics: {
          ...state.statistics,
          usedHints: state.statistics.usedHints + 1,
        },
      }
    case UPDATE_RESOLVED_WORDS:
      return {
        ...state,
        resolvedWords: state.resolvedWords.concat(action.payload),
      }
    default:
      return state
  }
}
