import React from "react"

import {
  Avatar,
  Gradient,
  Group,
  Header,
  Panel,
  PanelHeader,
  PanelHeaderBack,
  SimpleCell,
  Spacing,
  Text,
  Title,
} from "@vkontakte/vkui"
import { Icon28GraphOutline, Icon28StatisticsOutline } from "@vkontakte/icons"
import { Icon28ListCheckOutline } from "@vkontakte/icons"
import { useDispatch, useSelector } from "react-redux"
import { setPage } from "../store/router/actions"

const Profile = (props) => {
  const dispatch = useDispatch()
  const { first_name, last_name, photo_200 } = useSelector(
    (state) => state.vk.user
  )
  return (
    <Panel id={props.id}>
      {/* <PanelHeader left={<PanelHeaderBack />}></PanelHeader> */}
      <Gradient className='profile--gradient'>
        <Avatar size={96} src={photo_200} />
        <Title
          className='profile--title_user_name'
          level='2'
          weight='medium'
        >{`${first_name} ${last_name}`}</Title>
        <Text className='profile--text_user_rank' weight='regular'>
          {123}
        </Text>
      </Gradient>
      <Spacing />
      <Group mode='card' header={<Header mode='secondary'></Header>}>
        <SimpleCell
          before={<Icon28StatisticsOutline />}
          expandable
          onClick={() => dispatch(setPage("statistics"))}
        >
          
        </SimpleCell>
        <SimpleCell
          before={<Icon28ListCheckOutline />}
          expandable
          onClick={() => dispatch(setPage("resolvedWords"))}
        >
           
        </SimpleCell>
      </Group>
    </Panel>
  )
}

export default Profile
