import { categories } from "../../components/categories"
import HintsAlert from "../../components/HintsAlert"
import { words } from "../../words/words"
import { openPopout, openSnackbar, setPage } from "../router/actions"
import {
  INCREMENT_GAMES,
  INCREMENT_LOOSE,
  INCREMENT_USED_HINTS,
  INCREMENT_WINS,
  UPDATE_RESOLVED_WORDS,
} from "../vk/actionTypes"
import {
  WRONG_ANSWER,
  SET_THEME,
  SET_WORD,
  USE_HINT,
  CORRECT_ANSWER,
  LOSING,
  WINNING,
  CONTINUE_GAME,
  SWITCH_GAME_MODE,
} from "./actionsType"

export const setTheme = (theme, i) => {
  return (dispatch) => {
    if (!i) {
      let i = Math.round(1 - 0.5 + Math.random() * (1 - 1 + 8))
      let theme = categories[i].title
      i -= 1
      dispatch({ type: SET_THEME, payload: { theme, i } })
      dispatch(setWord())
      return dispatch(setPage("game"))
    }
    i -= 1
    dispatch({ type: SET_THEME, payload: { theme, i } })
    dispatch(setWord())
    return dispatch(setPage("game"))
  }
}
export const setWord = () => {
  return (dispatch, getState) => {
    let i = getState().game.theme.i
    let word = words[i][Math.floor(Math.random() * words[i].length)]
    word = word.split("")

    let answerArr = Array(word.length).fill("")

    let helth, currentHelth

    if (word.length <= 4) {
      helth = 4
    } else if (word.length <= 7) {
      helth = 6
    } else if (word.length <= 9) {
      helth = 8
    } else helth = 10

    currentHelth = helth
    dispatch({
      type: SET_WORD,
      payload: {
        word,
        answerArr,
        helth,
        currentHelth,
      },
    })
  }
}
export const answer = (answer) => {
  return (dispatch, getState) => {
    let {
      currentHelth,
      word,
      answerArr,
      correctLetters,
      wrongLetters,
      disabledButtons,
    } = getState().game

    if (disabledButtons) return

    if (!correctLetters.length && !wrongLetters.length) {
      dispatch({ type: INCREMENT_GAMES })
    }

    if (correctLetters.includes(answer) || wrongLetters.includes(answer))
      return dispatch(openSnackbar(` ${answer}  `, "repeat"))

    let i = word.indexOf(answer)

    if (i === -1) {
      currentHelth -= 1
      let wrongLet = wrongLetters.concat(answer)
      dispatch({ type: WRONG_ANSWER, payload: { currentHelth, wrongLet } })

      return dispatch(checkGame())
    }
    let newAnswerArr = answerArr.map((a, index) => {
      if (word[index] === answer) return word[index]
      return a
    })

    let correctLet = correctLetters.concat(answer)

    dispatch({
      type: CORRECT_ANSWER,
      payload: { newAnswerArr, correctLet },
    })
    return dispatch(checkGame())
  }
}
export const useHint = (answer) => {
  return (dispatch, getState) => {
    let {
      answerArr,
      word,
      disabledButtons,
      hints,
      correctLetters,
      wrongLetters,
    } = getState().game

    if (disabledButtons) return

    if (!hints) return dispatch(openPopout(<HintsAlert />))

    if (!correctLetters.length && !wrongLetters.length)
      dispatch({ type: INCREMENT_GAMES })

    let i = word.indexOf(answer)

    let wrongLet = wrongLetters.concat()
    let correctLet = correctLetters.concat()
    let newAnswerArr = answerArr.concat()

    if (correctLetters.includes(answer) || wrongLetters.includes(answer))
      return dispatch(openSnackbar("   !", "repeat"))

    if (i == -1) {
      hints -= 1

      wrongLet = wrongLetters.concat(answer)

      dispatch({
        type: USE_HINT,
        payload: { newAnswerArr, hints, correctLet, wrongLet },
      })

      return dispatch({ type: INCREMENT_USED_HINTS })
    }

    let letter = word[i]
    newAnswerArr = answerArr.map((a, index) => {
      if (word[index] === letter) return word[index]
      return a
    })

    correctLet = correctLetters.concat(letter)
    hints -= 1

    dispatch({
      type: USE_HINT,
      payload: { newAnswerArr, hints, correctLet, wrongLet },
    })
    dispatch({ type: INCREMENT_USED_HINTS })
    dispatch(checkGame())
  }
}

const checkGame = () => {
  return (dispatch, getState) => {
    let { answerArr, word, currentHelth } = getState().game

    if (!currentHelth) {
      dispatch(openSnackbar(" ", "losing"))
      answerArr = word
      dispatch({ type: LOSING, payload: answerArr })
      dispatch({ type: INCREMENT_LOOSE })
      return setTimeout(() => dispatch(setWord()), 2000)
    }

    if (answerArr.every((a) => a !== "")) {
      dispatch(openSnackbar(" ", "winning"))
      dispatch({ type: WINNING })
      dispatch({ type: INCREMENT_WINS })

      dispatch({
        type: UPDATE_RESOLVED_WORDS,
        payload: { word: word.join(""), date: Date.now() },
      })

      return setTimeout(() => dispatch(setWord()), 2000)
    }
  }
}

export const continueGame = () => {
  return (dispatch) => {
    dispatch({ type: CONTINUE_GAME })
    dispatch(setPage("game"))
  }
}

export const switchGameMode = (mode) => {
  return (dispatch, getState) => {
    let { hints } = getState().game

    if (!hints && mode === "hint") {
      dispatch(openPopout(<HintsAlert />))
      return document
        .getElementsByClassName("SliderSwitchIcons__button")[0]
        .click()
      // return setTimeout(
      //   () =>
      //     document
      //       .getElementsByClassName("SliderSwitchIcons__button")[0]
      //       .click(),
      //   150
      // )
    }

    dispatch({ type: SWITCH_GAME_MODE, payload: mode })
  }
}
