import React, { useEffect } from "react"

import { useDispatch, useSelector } from "react-redux"

import PropTypes from "prop-types"

import bridge from "@vkontakte/vk-bridge"
import {
  Panel,
  PanelHeader,
  Div,
  IconButton,
  Counter,
  Spacing,
  Tappable,
  PanelHeaderBack,
  FixedLayout,
  PromoBanner,
  SliderSwitch,
} from "@vkontakte/vkui"

import {
  Icon24Like,
  Icon24LikeOutline,
  Icon24Flash,
  Icon24Error,
  Icon24Done,
  Icon24Cancel,
} from "@vkontakte/icons/"

import { Icon28InfoOutline } from "@vkontakte/icons"
import { Icon28InfoCircleOutline } from "@vkontakte/icons"

import { Icon28LikeOutline } from "@vkontakte/icons"
import { Icon28Like } from "@vkontakte/icons"

import { Icon24TextOutline } from "@vkontakte/icons"
import { Icon28LightbulbOutline } from "@vkontakte/icons"

import { goBack } from "../store/router/actions"
import { answer, switchGameMode, useHint } from "../store/game/actions"

import SliderSwitchIcons from "../components/Slider/SliderSwitchIcons"

import { RULetters } from "../words/RULetters"
import IconsHelth from "../components/IconsHelth/IconsHelth"

const Game = (props) => {
  const dispatch = useDispatch()
  const {
    theme,
    disabledButtons,
    answerArr,
    hints,
    correctLetters,
    wrongLetters,

    helth,
    currentHelth,

    mode,
  } = useSelector((state) => state.game)

  const { statistics, resolvedWords } = useSelector((state) => state.vk)

  const { snackbar } = useSelector((state) => state.router)

  useEffect(() => {
    bridge.send("VKWebAppStorageSet", {
      key: "game",
      value: JSON.stringify({
        activeGame: {
          theme,
          answerArr,
          helth,
          currentHelth,
          correctLetters,
          wrongLetters,
        },
        hints,
      }),
    })
  }, [correctLetters, wrongLetters, hints])

  useEffect(() => {
    bridge
      .send("VKWebAppStorageSet", {
        key: "statistics",
        value: JSON.stringify(statistics),
      })
      .then((data) => {
        console.log(data.result)
      })
      .catch((err) => console.log(err))
  }, [statistics])

  useEffect(() => {
    bridge
      .send("VKWebAppStorageSet", {
        key: "resolvedWords",
        value: JSON.stringify(resolvedWords),
      })
      .then((data) => {
        console.log(data.result)
      })
      .catch((err) => console.log(err))
  }, [resolvedWords])

  return (
    <Panel id={props.id}>
      <PanelHeader
        left={<PanelHeaderBack onClick={() => dispatch(goBack())} />}
      >
        {theme.title}
      </PanelHeader>

      <Div className='Icons__helth'>
        <IconsHelth />
      </Div>

      <Div className='Word__container'>
        {answerArr.map((letter, i) => (
          <Tappable
            disabled={disabledButtons}
            activeEffectDelay={200}
            key={i}
            className='Word__letter'
            //onClick={() => dispatch(useHint(i))}
          >
            {letter}
          </Tappable>
        ))}
      </Div>

      <Spacing separator />

      <Div className='keybord'>
        <div className='letters'>
          {RULetters.map((a, i) => (
            <Tappable
              disabled={disabledButtons}
              onClick={() => {
                mode === "answer" ? dispatch(answer(a)) : dispatch(useHint(a))
              }}
              activeEffectDelay={200}
              style={{ width: 48 }}
              className='letter'
              key={i}
            >
              {a}
              {correctLetters.includes(a) && (
                <Icon24Done
                  className='iconLetter'
                  fill='var(--field_valid_border)'
                  width={20}
                  height={20}
                />
              )}
              {wrongLetters.includes(a) && (
                <Icon24Cancel
                  className='iconLetter'
                  fill='var(--field_error_border)'
                  width={20}
                  height={20}
                />
              )}
            </Tappable>
          ))}
        </div>

        <SliderSwitchIcons
          activeValue={mode}
          onSwitch={(e) => dispatch(switchGameMode(e))}
          options={[
            {
              icon: (
                <Icon24TextOutline
                  width={28}
                  height={28}
                  fill='var(--tabbar_active_icon)'
                />
              ),
              value: "answer",
            },
            {
              icon: (
                <>
                  <Icon28LightbulbOutline fill='var(--tabbar_active_icon)' />
                  <Counter size='m' mode='primary' className='Counter__hints'>
                    {hints}
                  </Counter>
                </>
              ),
              value: "hint",
            },
          ]}
        />
      </Div>

      {snackbar}
    </Panel>
  )
}

