import AppSnackbar from "../../components/AppSnackbar"
import {
  SET_STORY,
  SET_PAGE,
  GO_BACK,
  OPEN_SNACKBAR,
  CLOSE_SNACKBAR,
  OPEN_POPOUT,
  CLOSE_POPOUT,
} from "./actionTypes"

export const setStory = (story, panel) => {
  return (dispatch, getState) => {
    window.history.pushState(null, null)

    let storiesHistory = getState().router.storiesHistory
    let panelsHistory = getState().router.panelsHistory
    storiesHistory.push(story)

    panelsHistory.push(panel)

    return dispatch({
      type: SET_STORY,
      payload: { storiesHistory, panelsHistory, story: story, panel: panel },
    })
  }
}

export const setPage = (panel) => {
  return (dispatch, getState) => {
    window.history.pushState(null, null)
    let panelsHistory = getState().router.panelsHistory
    panelsHistory.push(panel)

    let activePanel = panel

    return dispatch({ type: SET_PAGE, payload: { activePanel, panelsHistory } })
  }
}

export const goBack = () => {
  return (dispatch, getState) => {
    //window.history.pushState(null, null)

    let { storiesHistory, panelsHistory, popout } = getState().router

    if (popout) return dispatch(closePopout())

    if (panelsHistory.length === 1 && storiesHistory.length === 1) return

    if (panelsHistory[panelsHistory.length - 1] === "game") {
      console.log("game")
    }

    if (panelsHistory[panelsHistory.length - 1] === "profile") {
      storiesHistory.pop()
    }

    panelsHistory.pop()

    let story = storiesHistory[storiesHistory.length - 1]
    let panel = panelsHistory[panelsHistory.length - 1]

    return dispatch({
      type: GO_BACK,
      payload: { storiesHistory, panelsHistory, story: story, panel: panel },
    })
  }
}
export const openPopout = (popout) => {
  return (dispatch) => {
    window.history.pushState(null, null)
    dispatch({ type: OPEN_POPOUT, payload: popout })
  }
}

export const closePopout = () => ({ type: CLOSE_POPOUT })

export const openSnackbar = (text, mode, duration) => ({
  type: OPEN_SNACKBAR,
  payload: <AppSnackbar text={text} mode={mode} duration={duration} />,
})

export const closeSnackbar = () => ({
  type: CLOSE_SNACKBAR,
})
