import React from "react"
import {
  List,
  Panel,
  PanelHeader,
  PanelHeaderBack,
  SimpleCell,
} from "@vkontakte/vkui"

import { Icon28GameOutline } from "@vkontakte/icons"
import { Icon28CheckCircleOutline } from "@vkontakte/icons"
import { Icon28CancelCircleOutline } from "@vkontakte/icons"
import { Icon28LightbulbStarOutline } from "@vkontakte/icons"

import { useDispatch, useSelector } from "react-redux"
import { goBack } from "../store/router/actions"

const Statistics = (props) => {
  const dispatch = useDispatch()
  const { statistics } = useSelector((state) => state.vk)
  return (
    <Panel id={props.id}>
      <PanelHeader
        left={<PanelHeaderBack onClick={() => dispatch(goBack())} />}
      >
        
      </PanelHeader>
      <List>
        <SimpleCell
          indicator={statistics.totalGames}
          disabled
          before={<Icon28GameOutline />}
        >
           :
        </SimpleCell>
        <SimpleCell
          indicator={statistics.wins}
          disabled
          before={<Icon28CheckCircleOutline />}
        >
          :
        </SimpleCell>
        <SimpleCell
          indicator={statistics.loose}
          disabled
          before={<Icon28CancelCircleOutline />}
        >
          :
        </SimpleCell>
        <SimpleCell
          indicator={statistics.usedHints}
          disabled
          before={<Icon28LightbulbStarOutline />}
        >
           :
        </SimpleCell>
      </List>
    </Panel>
  )
}
export default Statistics
